<template>
    <div>
        {{$route.params.id}}
        <h1 v-html="articleFS.title"></h1>
        <h1 v-html="articleFS.subtitle"></h1>
        <p v-html="articleFS.content"/>
    </div>
</template>

<script>
import { db } from '../db'

export default {
    props: {
        article: Object
    },
    data() {
        return {
            articleFS: {}
        }
    },
    mounted() {
        db.collection('documents')
        .doc(this.$route.params.id)
        .get()
        .then(snapshot => {
            const document = snapshot.data()
            this.articleFS = document
        })
    }
}
</script>